.project {
  margin: 50px 0px;
  margin-left: 120px;
}

.project hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.project h2 {
font-weight: bold;
color: #138781;
}

#ads {
  margin: 30px 0px 30px 0px;
}

#ads .card-notify-badge {
  position: absolute;
  left: -10px;
  top: -20px;
  background-color: #f29f67;
  text-align: center;
  border-radius: 30px;
  color: black;
  padding: 5px 10px;
  font-size: 14px;
}
.project .card img{
  height: 150px;
  width: 100%;
}

.card-detail-badge{
  background-color: #f4d661;
  text-align: center;
  border-radius: 30px;
  color: black;
  padding: 5px 10px;
  font-size: 15px;
}

.card-image-overly {
  font-size: 20px;
}
.card-image-overly span{
    display: inline-block;
    margin-left: 5px;
}

.project .card.card:hover{
  background-color: white;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  transition: all 1s ease;
}
.ad-btn {
  text-transform: uppercase;
  width: 150px;
  height: 40px;
  display: block;
  text-align: center;
  background-color: #138781;
  color: white;
  border-radius: 80px;
  font-size: 16px;
  line-height: 35px;
  text-decoration: none;
  margin: 20px auto 20px auto;
  overflow: hidden;
  position: relative;
}

.ad-btn:hover{
  background-color: white;
  color: #138781;
  background: transparent;
  border: 2px solid;
  transition: all 0.5s ease;
  box-shadow:  rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}


/* ================= DARK MODE ==================  */

#dark .project h2 {
  font-weight: bold;
  color: #89dfda;
  }

  #dark .project hr {
    color:  #89dfda;
    width: 400px;
    margin: 10px auto;
  }

  /* =============== Mobile View =============== */

  @media (max-width: 600px){
    .project {
      margin: 0;
      padding: 0;
      margin-top: 50px;
    }
    .project hr {
      width: 320px;
    }

    .project .card {
      margin-top: 30px;
    }
    
  }