.work {
  background-color: #1387812a;
  margin-left: 140px;
}

.work-experience{
  padding: 60px 0px;

}

.work-experience h2 {
  font-weight: bold;
  color: black;
}

.work-experience hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

/* ============== DARK MODE ============== */

#dark .work {
  background-color: #643a6b;
}

#dark .work-experience h2 {
  color: white;
}

/* =============== Mobile View ================= */

@media (max-width:600px){
  .work {
    margin: 5px;
    padding: 5px;
    margin-top: 50px;
  }

  .work hr {
    width: 310px;
  }
}